export default {
	data() {
		return {
			AllData: []
		}
	},
	onLoad() {
		uni.request({
		    url: 'https://weixin.dueape.com/dueape/class/index',
			method: 'POST',
			data: {
				order: 'desc',
				sortField: 'class_id',
				pageSize: 10,
				pageNum: 1,
				displayFlag: 1,
				courseType: undefined,
				collegeId: undefined
			},
		    success: (res) => {
				const data = res.data
				this.AllData = data.results.data
		    }
		})
	},
	methods: {
	}
}
